* {
  margin: 0;
  padding: 0;
}

.App {
  background: #1f1f1f;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 100vw;
  gap: 2rem;
  padding: 30px 0px 30px 0px;
}

.individual-card-container {
  flex-basis: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25vh;
  text-align: center;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  border: 1px solid whitesmoke;
}

.individual-card-container:hover {
  transform: scale(1.05);
}

.individual-card-container img {
  border-radius: 15px 15px 0px 0px;
  height: 13vh;
  width: 100%;
}

.individual-card-container h3 {
  padding-top: 17px;
}

.card-content-container {
  border-radius: 20px;
  height: 20vh;
  width: 15vw;
  background: white;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("https://i.pinimg.com/originals/ea/1d/97/ea1d9797c9bf3dda7a23b238e5e4b364.jpg");
  background-size: 100vw 700px;
  background-repeat: no-repeat;
  color: white;
  border-bottom: 1px solid white;
  gap: 1rem;
  padding: 15px;
}

@media (max-width: 999px) {
  .individual-card-container {
    height: 20vh;
  }
  .individual-card-container h3 {
    padding-top: 5px;
  }

  .card-content-container {
    height: 18vh;
    width: 18vw;
  }
}

@media (max-width: 835px) {
  .individual-card-container {
    flex-basis: 25%;
  }

  .card-content-container {
    height: 17vh;
    width: 20vw;
  }
}

@media (max-width: 710px) {
  .card-content-container {
    font-size: 1rem;
    width: 22vw;
  }
}

@media (max-width: 640px) {
  .card-content-container {
    font-size: 0.8rem;
    width: 22vw;
  }
}

@media (max-width: 540px) {
  .card-content-container {
    font-size: 0.6rem;
    width: 20vw;
  }
  .card-content-container h3 {
    padding-top: 18px;
  }
  .individual-card-container img {
    height: 10vh;
  }
}
